import { useEffect, useState } from "react";
interface DiscordProps {
    classNames?: string,
    inviteCode: string
}

interface IDiscordData {
    guild: {
        name: string,
        icon: string,
    }
}


export const DiscordCard: React.FC<DiscordProps> = (props) => {

    const [isLoading, setIsLoading] = useState(true);
    const [discordData, setDiscordData] = useState<IDiscordData>();

    useEffect(() => {
        const fetchData = async () => {
            const res = await fetch(`https://discord.com/api/v9/invites/${props.inviteCode}?with_counts=true&with_expiration=true`, {
                "body": null,
                "method": "GET"
            });
            setDiscordData(await res.json());
            setIsLoading(false);
        }

        fetchData();
    }, []);



    return (

        <div className={`p-4 bg-[#36393f] rounded flex flex-col items-center text-center font-Whitney ${props.classNames}`}>
            <div className="p-4">
                {isLoading ? <svg className="animate-pulse" height={64} width={64} >

                    <rect width={64} height={64} className="fill-gray-500 " rx={12}></rect>

                </svg> :
                    <img className="w-16 h-16 rounded-xl" src={`https://cdn.discordapp.com/icons/784752641136656415/${discordData?.guild.icon}.webp?size=128`}></img>
                }


            </div>
            <div className="text-[#b9bbbe]">Zaproszono Ci do doczenia</div>
            <div className={`rounded text-2xl text-white mt-2 font-semibold` + (isLoading ? ` bg-gray-500 select-none animate-pulse  w-full h-[32px]` : ``)}>{isLoading ? "" : `${discordData?.guild.name}`}</div>
            <button type="button" className="w-full [background-color:hsl(235,calc(1*85.6%),64.7%)] rounded p-2 mt-4" onClick={() => window.open('https://discord.com/invite/amen')}><div className="whitespace-nowrap overflow-ellipsis overflow-hidden">Zaakceptuj zaproszenie</div></button>
        </div >

    )
}