import { CommunityCard } from './CommunityCard';
import { DiscordCard } from './DiscordCard';
import yt from "./svgs/youtube-brands.svg";
import tw from "./svgs/twitch-brands.svg";
import ig from "./svgs/instagram-brands.svg";
import sc from "./svgs/soundcloud-brands.svg";
import sp from "./svgs/spotify-brands.svg";

export const Community = () => {


    return (
        <div className="flex flex-col items-center justify-start text-white bg-black p-2">
            <div className="text-xl md:text-2xl">Spoeczno</div>
            <div className="text-lg md:text-xl p-4 text-center">Tutaj znajdziesz wszystkie linki do spoecznoci, lepiej docz .</div>
            <div className="flex flex-wrap md:flex-row justify-evenly">
                <CommunityCard colorClass="bg-red-600" classNames='m-1'>
                    <img src={yt} alt="" className="w-10 invert" draggable={false} />
                </CommunityCard>

                <CommunityCard colorClass='bg-purple-600' classNames='m-1'>
                    <img src={tw} alt="" className="w-10 invert" draggable={false} />
                </CommunityCard>


                <CommunityCard classNames={"instagram m-1"}>
                    <img src={ig} alt="" className="w-10 invert" draggable={false} />

                </CommunityCard>

                <CommunityCard colorClass='bg-orange-600' classNames='m-1'>
                    <img src={sc} alt="" className="w-10 invert" draggable={false} />

                </CommunityCard>

                <CommunityCard colorClass='bg-green-600' classNames='m-1'>
                    <img src={sp} alt="" className="w-10 invert" draggable={false} />

                </CommunityCard>

            </div>
            <div className='flex flex-col justify-center items-center text-center mt-4 w-full'>

                Natomiast najwiksz spoeczno mamy na discordzie.
                {

                    <DiscordCard classNames='w-10/12 md:w-6/12 lg:w-1/4 mt-4' inviteCode='amen' />
                }

            </div>


        </div>


    )
}