import { Link } from "react-router-dom";
import bars from "./svgs/bars-solid.svg"


export const Nav = () => {
    const navLinks = [
        <Link to="/about" className="hover:underline active:underline">O mnie</Link>,
        <Link to="/links" className="hover:underline active:underline">Spoeczno</Link>,
        <Link to="/compose" className="hover:underline active:underline">Muzyka</Link>,

    ]

    document.addEventListener("click", async (ev) => {
        let navChildren: ChildNode[] = [];
        document.getElementById("mobileMenu")?.childNodes.forEach(n => {
            navChildren.push(n);
        })
        const menu = document.getElementById("mobileMenu");
        const menuButton = document.getElementById("menuButton");
        const menuSvg = document.getElementById("menuSvg");
        if (menu?.classList.contains("animateOut")) return;
        //@ts-ignore
        if (ev.target !== menu && ev.target !== menuButton && ev.target !== menuSvg && !navChildren.includes(ev.target)) {

            menu?.classList.add("animateOut");
            menu?.classList.remove("animateIn");
            menu?.classList.remove("ininitializeNav");



        }
    })

    return (
        <div>
            <div className="flex justify-end ">

                <div className="flex flex-col items-center  shadow-md md:hidden w-1/2 fixed h-full bg-black text-white z-10 border-green-600 border-l-2 border-b-2 initializeNav" id="mobileMenu">

                    <div className="py-2 border-green-600 border-b-2 w-full flex justify-center" key={"enigma"}>
                        <Link to="/">
                            <img src="enigma.png" alt="" className="h-10  invert select-none" />
                        </Link>
                    </div>

                    {/* Generating an array of nav links for from the array on the top */}
                    {
                        (() => {
                            const links: JSX.Element[] = [];
                            let counter = 0;
                            for (const navLink of navLinks) {
                                links.push(
                                    <div className="py-2 border-green-600 border-b-2 w-full flex justify-center" key={counter++}>
                                        {navLink}
                                    </div>
                                )
                            }

                            return links;
                        })()
                    }




                </div>


            </div>

            <nav className="flex flex-col text-white border-green-600 border-2 rounded bg-black">

                {/* side bar */}

                {/* nav backgorund */}
                <div className="flex justify-between py-4 px-4 md:px-20 lg:px-40 items-center  shadow-md">
                    {/* logo */}

                    <Link to="/">
                        <div className="flex items-center">
                            <img src="enigma.png" alt="" className="h-10 invert select-none" />
                        </div>
                    </Link>




                    {/* First nav, desktop */}
                    <div className="justify-evenly hidden sm:flex px-4">
                        {/* Generating an array of nav links for from the array on the top */}
                        {
                            (() => {
                                const links: JSX.Element[] = [];
                                let counter = 0;

                                for (const navLink of navLinks) {
                                    links.push(
                                        <div className="px-4" key={counter++}>
                                            {navLink}
                                        </div>
                                    )
                                }

                                return links;
                            })()
                        }
                    </div>

                    {/* Burger */}
                    <div className="flex sm:hidden invert">
                        <button onClick={() => {
                            const menu = document.getElementById("mobileMenu");
                            menu?.classList.remove("animateOut");

                            menu?.classList.add("animateIn")
                            menu?.classList.remove("initializeNav");

                            
                        }} id="menuButton"><img src={bars} className="w-8" id="menuSvg" /></button>
                    </div>
                </div>


            </nav>
        </div>

    );
}