//@ts-ignore
import { useEffect, useState } from 'react';
//@ts-ignore
import { Piano, KeyboardShortcuts, MidiNumbers } from 'react-piano';
import 'react-piano/dist/styles.css';
import DimensionsProvider from './DimensionsProvider';
import SoundfontProvider from './SoundfontProvider';
//@ts-ignore
import { Knob } from "react-rotary-knob";
import * as Tone from 'tone'
//@ts-ignore

//@ts-ignore
const audioContext = new (window.AudioContext || window.webkitAudioContext)();
const soundfontHostname = 'https://d1pzp51pvbm36p.cloudfront.net';

const noteRange = {
    first: MidiNumbers.fromNote('c3'),
    last: MidiNumbers.fromNote('f4'),
};
const keyboardShortcuts = KeyboardShortcuts.create({
    firstNote: noteRange.first,
    lastNote: noteRange.last,
    keyboardConfig: KeyboardShortcuts.HOME_ROW,
});

function ResponsivePiano(props: JSX.IntrinsicAttributes) {
    return (
        <DimensionsProvider>
            {({ containerWidth, containerHeight }: { containerWidth: number, containerHeight: number }) => (
                <SoundfontProvider
                    audioContext={audioContext}
                    hostname={soundfontHostname}
                    //@ts-ignore
                    render={({ isLoading, playNote, stopNote }) => (
                        <Piano
                            noteRange={noteRange}
                            width={containerWidth}
                            playNote={playNote}
                            stopNote={stopNote}
                            disabled={isLoading}
                            {...props}
                        />
                    )}
                />
            )}
        </DimensionsProvider>
    );
}

const synths: {
    48: Tone.Sampler | null,
    49: Tone.Sampler | null,
    50: Tone.Sampler | null,
    51: Tone.Sampler | null,
    52: Tone.Sampler | null,
    53: Tone.Sampler | null,
    54: Tone.Sampler | null,
    55: Tone.Sampler | null,
    56: Tone.Sampler | null,
    57: Tone.Sampler | null,
    58: Tone.Sampler | null,
    59: Tone.Sampler | null,
    60: Tone.Sampler | null,
    61: Tone.Sampler | null,
    62: Tone.Sampler | null,
    63: Tone.Sampler | null,
    64: Tone.Sampler | null,
    65: Tone.Sampler | null,
    66: Tone.Sampler | null,
    67: Tone.Sampler | null,
} = {
    48: null,
    49: null,
    50: null,
    51: null,
    52: null,
    53: null,
    54: null,
    55: null,
    56: null,
    57: null,
    58: null,
    59: null,
    60: null,
    61: null,
    62: null,
    63: null,
    64: null,
    65: null,
    66: null,
    67: null,


}

export const Compose = () => {

    const [synthsReady, setSynthsReady] = useState(false);
    const [file, setFile] = useState(null);
    const [release, setRelease] = useState(0);
    const [volume, setVolume] = useState(0);
    const [attack, setAttack] = useState(0);
    //@ts-ignore




    useEffect(() => {

        const toneSetter = async () => {
            if (!file) return;
            setSynthsReady(false);

            //@ts-ignore
            let audioBuffer = await audioContext.decodeAudioData(await file.arrayBuffer());

            for (const key in synths) {
                //@ts-ignore
                synths[key] = new Tone.Sampler({
                    //@ts-ignore
                    "C3": audioBuffer
                }).toDestination();

            }
            setSynthsReady(true);

        }
        toneSetter();

    }, [file])

    return (
        <div className="flex items-center text-white p-4 flex-col bg-black">
            <div className="text-center">


                <div className='text-xl'> <a href="https://soundcloud.com/enigma00730" rel="noreferrer" target="_blank" className="underline">Tutaj</a>, znajdziesz moj muzyk. S to ostre bengery .</div>
                <div className='p-4'>A tymczasem pokomponuj sobie mordo</div>
            </div>


            <div className="flex w-full sm:w-1/2 justify-center">
                <ResponsivePiano

                    //@ts-ignore
                    playNote={async (midiNumber: number) => {
                        //@ts-ignore
                        if (!synths[midiNumber]) return;
                        //@ts-ignore
                        synths[midiNumber].release = parseFloat(release.toFixed(1));
                        //@ts-ignore
                        synths[midiNumber].volume.value = volume;
                        //@ts-ignore
                        synths[midiNumber].attack = parseFloat(attack.toFixed(1));

                        //@ts-ignore
                        synths[midiNumber].triggerAttack(numberToChord(midiNumber));

                    }}
                    //@ts-ignore
                    stopNote={(midiNumber: number) => {
                        //@ts-ignore
                        if (!synths[midiNumber]) return;
                        //@ts-ignore
                        synths[midiNumber].triggerRelease(numberToChord(midiNumber));
                    }}
                    keyboardShortcuts={keyboardShortcuts}
                    disabled={synthsReady ? false : true}
                />

            </div>
            <div className="flex items-center flex-col ">
                <div className="text-center p-4">Wgraj plik ktry moesz zagra na pianinie albo keyboardzie? Nie wiem jak to sie nazywa.</div>

            </div>
            <div className="flex justify-center ">
                {/* @ts-ignore */}
                <input className='flex justify-center text-sm w-1/2' type="file" name="" id="uploadFile" onChange={e => setFile(e.target.files?.item(0))} accept=".mp3,.wav" />

            </div>

            <div className="flex justify-between pt-4">
                <div className='flex flex-col items-center'>
                    <Knob preciseMode={false} defaultValue={0} min={0} max={1.0} onChange={(v: number) => setAttack(v)} step={0.1} />
                    <div className='text-center p-4'>Attack: {attack.toFixed(1)}</div>
                </div>
                <div className='flex flex-col items-center'>
                    <Knob preciseMode={false} defaultValue={0} min={0} max={1.0} onChange={(v: number) => setRelease(v)} step={0.1} />
                    <div className='text-center p-4'>Release: {release.toFixed(1)}</div>
                </div>
                <div className='flex flex-col items-center'>
                    <Knob unlockDistance={0} defaultValue={0} min={20} max={-100} onChange={(v: number) => setVolume(v)} />
                    <div className='text-center p-4'>Volume: {volume.toFixed(1)}dB</div>
                </div>

            </div>


        </div>
    )
}


function numberToChord(number: number) {
    return {
        48: "C3",
        49: "C#3",
        50: "D3",
        51: "D#3",
        52: "E3",
        53: "F3",
        54: "F#3",
        55: "G3",
        56: "G#3",
        57: "A3",
        58: "A#3",
        59: "B3",
        60: "C4",
        61: "C#4",
        62: "D4",
        63: "D#4",
        64: "E4",
        65: "F4",
        67: "F#4"

    }[number];
}
