import ProgressiveImage from "react-progressive-image";

export const Main = () => {

    return (
        <div className="flex text-white items-center justify-center p-4 bg-black">
            <div className="flex flex-col sm:flex-row items-center w-full md:w-8/12 lg:w-1/2">
                <div className="flex sm:hidden justify-center ">
                    <ProgressiveImage
                        src="nygmon.png"
                        placeholder="nygmon-smaller.png"
                    >
                        {(src: string | undefined) => <img src={src} alt="an image" className="h-auto w-1/2 min-h-0 max-h-96"/>}
                    </ProgressiveImage>
                </div>
                <div className="flex flex-col p-4 w-full sm:w-5/6 xl:w-2/3">
                    <div className="text-2xl lg:text-4xl flex neonText font-neonderthaw">E<div className="animate-flicker">nig</div>ma</div>
                    <div className="text-sm sm:text-md md:text-lg lg:text-xl">To ja, Enigma (Ewenement Nowoczesnej Interkontynentalnej Gwarancji Masowych Atrakcji).
                        Dzban do zada specjalnych, alkoholik, pracoholik, montaysta, producent, raper. Moim gwnym zajciem jest streamowanie, granie oraz chlanie na skal wiatow.






                    </div>

                </div>

                <div className="hidden sm:flex justify-center ">
                    <img src="nygmon.png" alt="enigma" />
                </div>
            </div>


        </div>
    )
}