import React from "react";

interface CardProps {
    colorClass?: string,
    classNames?: string
}

export const CommunityCard: React.FC<CardProps> = (props) => {
    return (
        <div className={`${props.colorClass} rounded p-4 flex justify-center items-center select-none ${props.classNames}`}>
            {props.children}
        </div>
    );
}  
